<template>
  <section class="my-collention">
    <header-view :title="title" :fixed="fixed" :routerName="routerName" :rightText="rightText" @rightClick="parentEvent"></header-view>
    <tab-view :action="tabAction" @changeTab="changeTab" :activeId="active"></tab-view>
    <section>
        <div class="hi-life-list-item" v-for="(item,key) in list">
          <div v-if="edit" class="hi-life-list-checkbox">
            <i :class="{'checkbox-selected':selectArr.indexOf(item.goodId||item.id)>=0}" @click="check(item.goodId||item.id)"></i>
          </div>
          <div class="hi-life-list-item-img"><img :src="item.imageUrl"></div>
          <div class="hi-life-list-item-content">
            <h3>{{item.name}}</h3>
            <div class="hi-life-list-item-summary">{{item.summary}}</div>
            <div class="hi-life-list-item-other" v-if="item.skuId!=undefined">￥<span class="int-string">{{item.sellPrice|formatInt}}</span>{{item.sellPrice|formatDecimal}}
              <div class="hi-life-list-item-oper" v-if="!edit" @click="addCart1(item.skuId)"></div>
            </div>
          </div>
        </div>
    </section>
    <div class="my-collection-bottom" v-if="edit">
      <div class="hi-life-list-checkboxall"><i :class="{'checkbox-selected':selectArr.length===list.length&&selectArr.length!=0}" @click="checkAll"></i></div>
      <span>全选</span>
      <div class="dele-btn" @click="dele">取消收藏</div>
    </div>
  </section>
</template>

<script>
  import {getMyCollection,getMyCollectStore,cancelMyCollect,addCart} from '../../service/getData'
  import header from '@/components/header/header'
  import tab from '@/components/tab/tab'
  export default {
    components: {
      'header-view': header,
      'tab-view': tab
    },
    data () {
      return {
        title: '我的收藏',
        fixed: false,
        routerName: 'custom',
        rightText: '编辑',
        isBind: true,
        active: 'goods',
        list: [],
        edit: false,
        selectArr: [],
        classifyList: [],
        tabId: '0',
        tabAction: [
          {
            id: 'goods',
            categoryName: '商品'
          },
          {
            id: 'store',
            categoryName: '商家'
          }
        ]
      }
    },
    created: function () {
      var _this = this
      getMyCollection()
        .then(function (response) {
          _this.list = response.data
        })
        .catch(function () {
          _this.$toast('加载信息失败!')
        })
    },
    filters: {
      formatInt: function (val) {
        if (val.toString().indexOf('.') < 0) {
          return val
        } else {
          return val.toString().substr(0, val.toString().indexOf('.'))
        }
      },
      formatDecimal: function (val) {
        if (val.toString().indexOf('.') < 0) {
          return ''
        } else {
          return val.toString().substr(val.toString().indexOf('.'))
        }
      }
    },
    methods: {
      changeTab: function (val) {
        let _this = this
        this.active = val
        if(val === 'store'){
          getMyCollectStore()
          .then(res => {
            _this.list = res.data
          })
          .catch(res => {
            _this.$toast('请求失败')
          })
        } else {
          getMyCollection()
          .then(function (response) {
            _this.list = response.data
          })
          .catch(function () {
            _this.$toast('加载信息失败!')
          })
        }
      },
      goStore: function (val) {
        this.$router.push({
          name: 'store',
          query: {
            storeId: val
          },
          params: {
            prevName: 'myCollection'
          }
        })
      },
      parentEvent: function () {
        if (this.edit) {
          this.edit = false
          this.rightText = '编辑'
          this.selectArr = []
        } else {
          this.edit = true
          this.rightText = '完成'
        }
      },
      check: function (val) {
        this.selectArr.push(val)
        console.log(this.selectArr)
      },
      addCart1: function (val) {
        var _this = this
        addCart(val)
          .then(res => {
            if (res.data.code === 1) {
              _this.$toast('成功加入购物车')
            }
          })
          .catch(() => {
            _this.$toast('添加失败')
          })
      },
      dele: function () {
        let _this = this
        this.edit = false
        cancelMyCollect(this.selectArr.join())
        .then(res => {
          if (res.data.code === 1) {
            if(this.active === 'store'){
              getMyCollectStore()
              .then(res => {
                _this.list = res.data
              })
              .catch(res => {
                _this.$toast('请求失败')
              })
            } else {
              getMyCollection()
              .then(res => {
                _this.list = res.data
              })
              .catch(res => {
                _this.$toast('加载信息失败!')
              })
            }
          }
        })
        .catch(res => {
          console.log(res)
          _this.$toast('取消收藏失败')
        })
      },
      checkAll: function () {
        let key = 0
        for (key in this.list) {
          if (this.list[key].skuId === undefined) {
            this.selectArr.push(this.list[key].id)
          } else {
            this.selectArr.push(this.list[key].skuId)
          }
        }
        console.log(this.selectArr)
      }
    }
  }
</script>

<style>
.my-collention .hi-life-list-item{
    padding-left: .15rem;
  }
.my-collention {
  height: 100%;
}
.mint-tab-container {
    overflow: auto;
    position: relative;
    height: 86%;
}
.my-collection-bottom {
  position: fixed;
  height: .45rem;
  line-height: .45rem;
  bottom: 0;
  width: 100%;
  z-index: 1;
  border-top: .01rem solid rgba(141, 138, 142, .2);
}

.hi-life-list-checkboxall {
  display: inline-block;
  width: .43rem;
  height: 100%;
  position: relative;
}

.hi-life-list-checkboxall i {
  position: absolute;
  top: 50%;
  left: 50%;
  line-height: normal;
  transform: translate(-50%, -50%);
  text-align: center;
  font-size: .2rem;
  width: .2rem;
  height: .2rem;
  border-radius: 50%;
  border: .01rem solid lightgray;
}

.my-collection-bottom span {
  vertical-align: top;
  color: #333;
}

.dele-btn {
  height: 100%;
  float: right;
  width: 1.05rem;
  color: #FFFFFF;
  text-align: center;
  background: -webkit-linear-gradient(right, top, rgb(223, 10, 23), rgb(255, 139, 32));
  background: linear-gradient(to left top, rgb(223, 10, 23), rgb(255, 139, 32));
}
</style>


